/* ALL */
.Images_Images__3iJJM {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-top: 6rem;
}

.Images_Wrapper__2ppQb {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #111;
    border-radius: 0.5rem;
    padding-top: 1rem;
    padding-bottom: 2rem;
    margin-bottom: 3rem;
}
.Images_Info__3BRXo {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
}
.Images_Content__VJRQw, .Images_Buttons__1grQ5 {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.Images_Heading__3SXPK {
    color: var(--color-purple);
}

/* IMAGES */
.Images_Repository__20WXi {
    width: 25rem;
}
.Images_Tag__2py_I {
    width: 10rem;
}
.Images_Id__1JOUt {
    width: 10rem;
}
.Images_Created__2mBzF {
    width: 9.5rem;
}
.Images_Size__21Ca3 {
    width: 15rem;
}

.Images_Repository__20WXi, .Images_Tag__2py_I, .Images_Id__1JOUt, .Images_Created__2mBzF,
.Images_Size__21Ca3 {
    overflow: hidden;
    margin-right: 1.25rem;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.Images_Button__2pw-n {
    margin-right: 1.25rem !important;
}

.Images_Headline__3TC_Z {
    color: var(--color-purple);
}

.Images_Images__3iJJM h1,
.Images_Images__3iJJM h2 {
    font-weight: 500;
}

.Images_Images__3iJJM h1 {
    font-size: 1.8rem;
    margin-top: 0;
    margin-bottom: 0.8rem;
}

.Images_Images__3iJJM h2 {
    font-size: 1.6rem;
}

.Images_Images__3iJJM p {
    font-size: 1.1rem;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
}

.Images_DetailTitle__MVEYP {
    color: var(--color-purple);
    width: 4.5rem;
}

.Images_Error__37yNz {
    color: red;
    white-space: pre;
    text-align: left !important;
}

/* desktop 1200 + */
@media (min-width: 1200px) {
    .Images_Images__3iJJM {
        margin-top: 10rem;
    }

    .Images_Wrapper__2ppQb {
        width: 90%;
        padding: 4.5rem 2rem 5rem 2rem;
    }
    .Images_Info__3BRXo {
        flex-direction: column;
        justify-content: space-around;
        margin-top: 4rem;
    }

    .Images_Images__3iJJM h1 {
        font-size: 2.5rem;
    }

    .Images_Images__3iJJM p {
        margin-bottom: 1rem;
    }

    /* IMAGES */
    .Images_Repository__20WXi {
        width: 20rem;
    }
    .Images_Tag__2py_I {
        width: 5rem;
    }
    .Images_Id__1JOUt {
        width: 8rem;
    }
    .Images_Created__2mBzF {
        width: 9.5rem;
    }
    .Images_Size__21Ca3 {
        width: 5rem;
    }
}

/* tabletLandscape 900 - 1199 */
@media (max-width: 1199px) {
    .Images_Repository__20WXi {
        width: 20rem;
    }
    .Images_Tag__2py_I {
        width: 4rem;
    }
    .Images_Id__1JOUt {
        width: 7rem;
    }
    .Images_Created__2mBzF {
        width: 7rem;
    }
    .Images_Buttons__1grQ5 {
        margin-top: 1rem;
        margin-left: 1rem;
    }
    .Images_DetailTitle__MVEYP {
        margin-left: 1.5rem !important;
    }
}

/* tabletPortrait 600 - 899 */
@media (max-width: 899px) {
    .Images_Repository__20WXi {
        width: 17rem;
    }
    .Images_Id__1JOUt {
        width: 7rem;
    }
    .Images_Buttons__1grQ5 {
        flex-direction: column-reverse;
    }
    .Images_Button__2pw-n {
        margin-top: 0.75rem !important;
    }
    .Images_DetailTitle__MVEYP {
        margin-left: 1.5rem !important;
    }
}

/* phone < 600 */
@media (max-width: 599px) {
    .Images_Repository__20WXi {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        word-break: break-word;
        margin-bottom: 1rem;
        margin-right: 0;
    }
    .Images_Size__21Ca3 {
        width: 8rem;
    }
    .Images_DetailTitle__MVEYP {
        margin-left: 0.2rem !important;
    }
}

